.duino {
  width: 95%;
  height: 45%;
  display: flex;
  flex-direction: column;
  background-color: white;
  padding-left: 1.25vh;
  padding-right: 1.25vh;

  &__project {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    margin: auto;
    margin-top: 12.5vh;

    &-description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: auto;
      margin-bottom: 5vh;
    }

    &-challenge {
      background-position: 35% 0;
      background-repeat: no-repeat;
      background-size: 40%;
      background-image: url("/images/projects/common/project_challenge.png");
    }

    &-approach {
      background-position: 35% 0;
      background-repeat: no-repeat;
      background-size: 40%;
      background-image: url("/images/projects/common/project_approach.png");
    }

    &-info {
      display: flex;
      flex-direction: column;
      filter: grayscale(100%);
      p {
        margin: auto auto auto auto;
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
      }

      .lab{
        margin: auto auto auto auto;
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
        margin-top: 1vh;
        margin-bottom: 1vh;
        width: 5vh;
        height: auto;
        opacity: 0.5;
      }

      img {
        margin: auto auto auto auto;
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
        margin-top: 1vh;
        margin-bottom: 1vh;
        width: 10vh;
        height: auto;
        opacity: 0.5;
      }
    }

    &-information {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 75%;
      height: fit-content;
      margin: auto;
      text-align: center;

      .a {
        left: 0;
        width: 45%;
        align-self: start;
      }

      .b {
        width: 45%;
        align-self: center;
      }

      .c {
        right: 0;
        width: 45%;
        align-self: flex-end;
      }

      div {
        width: 33%;
        background-color: white;
        padding-left: 0.75vh;
        padding-right: 0.75vh;
      }

      h2 {
        color: $secondary-color;
        font-size: 1.5em;
      }

      &-header {
        width: 100%;
        height: 50vh;
        display: flex;
        flex-direction: row;
        background-position: center;
        background-size: cover;
        background-image: url("/images/projects/duino/project/duino_project_pattern.png");
        background-color: white;
      }
    }

    &-decoration {
      font-size: 10em;
      text-align: center;
      margin-top: auto;
      margin-bottom: auto;
      margin-left: auto;
      top: 0;
      bottom: 0;
    }

    &-title {
      width: 60%;
      margin: auto auto auto auto;
      top: 0;
      bottom: 0;
    }

    &-discover {
      img {
        width: 100%;
        height: auto;
      }
    }

    &-develop {
    }

    &-deliver {
      &-img {
        text-align: center;
        width: 75vh;
        height: auto;
        margin: auto auto auto auto;
        top: 0;
        bottom: 0;
      }
    }

    &-greeting {
      height: 40vh;
      background-position: bottom;
      background-repeat: no-repeat;
      background-size: cover;
      background-image: url("/images/projects/duino/project/duino_project_greeting.png");
      p {
        background: white;
      }
    }
  }

  &__cover {
    width: 100%;
    height: 20vh;
    position: relative;
    background-size: cover;
    background-position: center;
    filter: grayscale(100%);
    background-image: url("/images/projects/duino/thumbnail/duino_thumbnail_background.jpg");
    transition: 1.5s;
  }

  &__cover:hover {
    filter: grayscale(0);
    transition: 1.5s;
    .duino__imageCover {
      opacity: 1;
      bottom: 5vh;
      transition: 1.5s;
    }
  }

  &__gradient {
    height: 7vh;
    width: 100%;
    padding: 1;
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    background: rgb(255, 255, 255);
    background: linear-gradient(0deg, rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, 0) 100%);
  }

  &__imageCover {
    height: 15vh;
    width: auto;
    position: absolute;
    left: 0;
    bottom: 0;
    opacity: 0;
    z-index: 1;
    transition: 1.5s;
  }

  &__information {
    width: 100%;
    height: 15vh;
    display: flex;
    flex-direction: row;
    background-color: white;
  }

  &__decoration {
    font-size: 6em;
    text-align: center;
  }

  &__text {
    width: 60%;
    margin: auto auto auto auto;
    top: 0;
    bottom: 0;

    p {
      color: #514f54;
      font-family: $main-font, sans-serif;
      font-weight: 800;
      opacity: 0.5;
      font-size: 0.725em;
    }

    h2 {
      color: #514f54;
    }
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
